import Swiper from 'swiper/bundle';

const initProductsSlider = (obj) => {

  const swiper = new Swiper(`${obj.slider} .swiper`, {

    navigation: {
      nextEl: obj.nextEl,
      prevEl: obj.prevEl,
    },

    breakpoints: {
      325: {
        spaceBetween: 10,
        slidesPerView: 2,
        grid: {
          rows: 2,
          fill: 'row'
        }
      },

      768: {
        spaceBetween: 15,
        slidesPerView: 3,
        grid: {
          rows: 1,
          fill: 'row'
        }
      },

      1200: {
        spaceBetween: 15,
        slidesPerView: 4,
        grid: {
          rows: 1,
          fill: 'row'
        }
      },

      1440: {
        spaceBetween: 20,
        slidesPerView: 4,
        grid: {
          rows: 1,
          fill: 'row'
        }
      }
    }

  });

};

const swiper = new Swiper(`.hero-hits .swiper`, {

  spaceBetween: 10,
  slidesPerView: 2,

  navigation: {
    nextEl: '.hero-hits .products-slider__arrow--next',
    prevEl: '.hero-hits .products-slider__arrow--prev'
  },
  
});


initProductsSlider({
  slider: '.hits-slider',
  nextEl: '.hits-slider .products-slider__arrow--next',
  prevEl: '.hits-slider .products-slider__arrow--prev'
});

initProductsSlider({
  slider: '.novelty-slider',
  nextEl: '.novelty-slider .products-slider__arrow--next',
  prevEl: '.novelty-slider .products-slider__arrow--prev'
});

initProductsSlider({
  slider: '.extra-slider',
  nextEl: '.extra-slider .products-slider__arrow--next',
  prevEl: '.extra-slider .products-slider__arrow--prev'
});