const orderModule = () => {

    try {
        
        const radios = document.querySelectorAll('.order-tabs__radio input');
        const contentItems = document.querySelectorAll('.order-tabs__content-item');
    
        const privateHouseCheckbox = document.querySelector('#private-house');
        const addressFields = document.querySelectorAll('.order-tabs__fields-block');
    
        const paymentItems = document.querySelectorAll('.order-block__radio input');


        const showFieldsForPrivateHouse = () => {
            addressFields[0].classList.add('-js-visible');
            addressFields[1].classList.remove('-js-visible');
        }
    
        const hideFieldsForPrivateHouse = () => {
            addressFields[0].classList.remove('-js-visible');
            addressFields[1].classList.add('-js-visible');
        }
    
        privateHouseCheckbox.addEventListener('change', (e) => {
    
            const target = e.target;
    
            target.checked ? showFieldsForPrivateHouse() : hideFieldsForPrivateHouse();
    
        });
    
        radios.forEach((radio, index) => {
    
            radio.addEventListener('change', (e) => {
    
                e.stopPropagation();
    
                const target = e.target;
                const radioDataAttr = target.getAttribute('data-tab-radio');
    
                contentItems.forEach(item => {
    
                    const itemDataAttr = item.getAttribute('data-tab-content');
    
                    item.classList.remove('-js-active');
    
                    radioDataAttr === itemDataAttr ? item.classList.add('-js-active') : '';
                    radioDataAttr === 'tab-1' ? paymentItems[0].setAttribute('disabled', true) : paymentItems[0].removeAttribute('disabled');
    
    
                });
    
            });
    
        });
    
    } catch (error) {
        
    }

};

orderModule();