import Swiper from 'swiper/bundle';

const productsInfoSliders = () => {
    try {
        let galleryTop = new Swiper('.product-info__main-slider', {
            spaceBetween: 15,
        });
        
        let galleryThumbs = new Swiper('.product-info__thumbs-slider', {
            slidesPerView: 3,
        
            navigation: {
                prevEl: '.product-info__thumbs-button--prev',
                nextEl: '.product-info__thumbs-button--next',
            },

            breakpoints: {
                325: {
                    spaceBetween: 15,
                    direction: 'horizontal',
                },

                475: {
                    direction: 'vertical',
                    spaceBetween: 15,
                },

                1024: {
                    spaceBetween: 25,
                    direction: 'vertical',
                }
            }
        });
        
        galleryTop.controller.control = galleryThumbs;
        galleryThumbs.controller.control = galleryTop;
    } catch (error) {
        
    }
}

const tabsModule = (obj) => {

    try {
        const buttons = document.querySelectorAll(obj.button);
        const content = document.querySelectorAll(obj.item);
    
        buttons.forEach(el => {
    
            el.addEventListener('click', (e) => {
    
                const buttonDataAttribute = el.getAttribute('data-tab-button')
    
                buttons.forEach(el => {
                    el.classList.remove('-js-active');
                });
    
                el.classList.add('-js-active');
    
                content.forEach(el => {
    
                    const contentDataAttribute = el.getAttribute('data-tab-content')
    
                    el.classList.remove('-js-active');
    
                    if (buttonDataAttribute == contentDataAttribute) {
                        el.classList.add('-js-active');
                    };
                });
    
            });
        });
    } catch (error) {
        
    }


};

tabsModule({
    button: '.product-info__tabs-button',
    item: '.product-info__tabs-item',
})

productsInfoSliders();