import Swiper from 'swiper/bundle';

const heroSlider = () => {
  try {
    const heroSwiper = new Swiper('.hero-slider', {

        spaceBetween: 15,

        // Navigation arrows
        navigation: {
          nextEl: '.hero-slider__button--next',
          prevEl: '.hero-slider__button--prev',
        },
    });

    window.innerWidth <= 768 ? heroSwiper.destroy() : '';
  } catch (error) {
    
  }
}

heroSlider();