const initCatalogeModal = () => {
    try {
      
      const triggers = document.querySelectorAll('.cataloge-modal__type-link');
      const contentItems = document.querySelectorAll('.cataloge-modal__content-item');
  
      const openCatalogeButton = document.querySelector('.header__cataloge-button');
      const catalogeModal = document.querySelector('.cataloge-modal');
      const catalogeModalContent = document.querySelector('.cataloge-modal__products');
  
      const catalogeTypes = document.querySelector('.cataloge-modal__aside');
      const catalogeContent = document.querySelector('.cataloge-modal__content');
      const catalogeModalReturnButton = document.querySelectorAll('.cataloge-modal__return-button');
  
      const body = document.querySelector('body');
      const header = document.querySelector('header');
  
      const setInitialType = (obj) => {
  
          obj.items.forEach((item, index) => {
  
              index === 0 ? item.classList.add(obj.className) : '';
  
          });
  
      };
  
      const clearTypeData = (obj) => {
  
          obj.items.forEach(item => {
  
              item.classList.remove(obj.className);
  
          });
  
      };
  
      const toggleCatalogeModal = () => {
  
          const paddingOffset = `${window.innerWidth - document.body.offsetWidth}px`;
  
          setTimeout(() => {
              
              catalogeModal.classList.add('-js-visible');
  
              body.style.overflow = 'hidden';
              body.style.paddingRight = paddingOffset;
              
              header.classList.contains('-js-fixed') ? header.style.paddingRight = paddingOffset : '';
  
          }, 100);
  
          setInitialType({items: triggers, className: '-js-hovered'});
          setInitialType({items: contentItems, className: '-js-visible'});
          
      };
  
      const closeCatalogeModal = () => {
  
          catalogeModal.classList.remove('-js-visible');
          
          setTimeout(() => {
              
              body.style.overflow = 'visible';
              body.style.paddingRight = 0;
  
              header.classList.contains('-js-fixed') ? header.style.paddingRight = 0 : '';
  
          }, 100);
  
          clearTypeData({items: triggers, className: '-js-hovered'});
          clearTypeData({items: contentItems, className: '-js-visible'});
  
      };
  
      const showCatalogeContent = () => {
          catalogeContent.classList.add('-js-visible');
          catalogeTypes.classList.add('-js-hide')
      };
  
      const hideCatalogeContent = () => {
          catalogeContent.classList.remove('-js-visible');
          catalogeTypes.classList.remove('-js-hide')
      };
  
      const clearEventsListener = (type, listener) => {
  
          triggers.forEach(trigger => {
  
              trigger.removeEventListener(type, listener);
  
          });
  
      };
  
      const mouseEnterListener = (e) => {
  
          const currentTarget = e.currentTarget;
          const triggerDataAttr = currentTarget.getAttribute('data-cataloge-trigger');
  
          clearTypeData({items: triggers, className: '-js-hovered'});
  
          contentItems.forEach(item => {
              const contentDataAttr = item.getAttribute('data-cataloge-content');
  
              item.classList.remove('-js-visible');
  
              if (triggerDataAttr === contentDataAttr) {
                  item.classList.add('-js-visible');
                  currentTarget.classList.add('-js-hovered');
              };
          });
  
      };
  
      const clickListener = (e) => {
  
          e.preventDefault();
  
          const currentTarget = e.currentTarget;
          const triggerDataAttr = currentTarget.getAttribute('data-cataloge-trigger');
  
          const triggerText = currentTarget.querySelector('.cataloge-modal__type-title').textContent;
  
          clearTypeData({items: triggers, className: '-js-hovered'});
  
          contentItems.forEach(item => {
  
              const catalogeModalContentTitle = item.querySelector('.cataloge-modal__content-title');
              const contentDataAttr = item.getAttribute('data-cataloge-content');
  
              item.classList.remove('-js-visible');
  
              if (triggerDataAttr === contentDataAttr) {
                  item.classList.add('-js-visible');
                  currentTarget.classList.add('-js-hovered');
                  catalogeModalContentTitle.textContent = triggerText;
                  showCatalogeContent();
              };
  
          });
  
      };
  
  
      triggers.forEach(trigger => {
  
          trigger.addEventListener('mouseenter', mouseEnterListener);
          trigger.addEventListener('click', clickListener);
  
      });
  
      window.innerWidth < 1024 ? clearEventsListener('mouseenter', mouseEnterListener) : clearEventsListener('click', clickListener);
  
      openCatalogeButton.addEventListener('click', toggleCatalogeModal);
  
      catalogeModalReturnButton.forEach(button => {
          button.addEventListener('click', hideCatalogeContent);
      });
  
      document.addEventListener('click', (e) => {
  
          const withinBoundaries = e.composedPath().includes(catalogeModalContent);
  
          if (catalogeModal.classList.contains('-js-visible')) {
  
              !withinBoundaries ? closeCatalogeModal() : '';
  
          };
          
      });
  
    } catch (error) {
      
    }
  };
  
  initCatalogeModal();